  color: #73877B;
}
.calc-select-input.error::placeholder {
  color: #DD7A02;
}

.scroll-wrapper {
  overflow: hidden;
  position: absolute;
  top: 76px;
  left: 0;
  z-index: 1;
  width: 100%;
  background-color: #fff;
  border-bottom-left-radius: clamp(16px, 0.4rem + 1.5vw, 20px);
  border-bottom-right-radius: clamp(16px, 0.4rem + 1.5vw, 20px);
  cursor: pointer;
}

.calc-select-dropdown {
  display: none;
  width: 100%;
  max-height: 200px;
  margin-top: 20px;
  background-color: #fff;
  overflow: auto;
  z-index: 1;
}
.calc-select-dropdown.open {
  display: block;
}

::-webkit-scrollbar {
  width: 10px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
  height: 100px;
  border-radius: 20px;
  overflow: hidden;
}

::-webkit-scrollbar-thumb {
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  background: #FFD74A;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #F9A207;
}

.calc-select-dropdown-item {
  padding: 16px 24px 16px 24px;
  color: #171C1A;
  transition: all 0.3s ease-out;
}
.calc-select-dropdown-item:hover {
  background-color: #FFD74A;
}

.calc-next-step {
  width: 100%;
  max-width: 400px;
  transition: all 0.3s ease-out;
  opacity: 0;
}
.calc-next-step.show {
  opacity: 1;
}

.step-two-item {
  cursor: pointer;
}

.step-two-picture {
  overflow: hidden;
  width: clamp(324px, 17.125rem + 12.5vw, 424px);
  height: 100%;
  margin-bottom: 12px;
  border: 2px solid transparent;
  transition: all 0.3s ease-out;
}

.active .step-two-picture {
  border: 2px solid #FFE888;
}

.step-two-picture-descr {
  text-align: center;
  transition: all 0.3s ease-out;
}

.active .step-two-picture-descr {
  color: #FFE888;
}

.calc-step-four-res-text {
  text-align: center;
  max-width: 512px;
  opacity: 0;
  transition: all 0.3s ease-out;
}
.calc-step-four-res-text.show {
  opacity: 1;
}
.calc-step-four-res-text h2 {
  margin-bottom: 12px;
}

.content-wrapper {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.img-container {
  grid-area: 1/1/6/6;
  border-radius: 20px;
  overflow: hidden;
}
.img-container img {
  width: 100%;
}

.paggination-container {
  top: 10px;
  position: relative;
  left: -8px;
  grid-area: 5/1/6/3;
  background-color: red;
  border: 20px solid #182825;
  border-radius: 30px;
}
.paggination-container::before {
  content: "";
  position: absolute;
  display: block;
  top: -50px;
  left: -33px;
  z-index: 1;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border-bottom-left-radius: 40px;
  border-left: 20px solid #182825;
  border-bottom: 20px solid #182825;
  box-shadow: inset;
}
.paggination-container::after {
  content: "";
  position: absolute;
  display: block;
  right: -49px;
  bottom: -30px;
  z-index: 1;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border-bottom-left-radius: 40px;
  border-left: 20px solid #182825;
  border-bottom: 20px solid #182825;
  box-shadow: inset;
}

.bottom-right-img-container {
  grid-area: 5/3/6/6;
}

@media screen and (min-width: 620px){
  .calc-svg-wrapper {
    width: clamp(20px, -1.2rem + 9vw, 72px);
  }
  .calc-svg-wrapper::after {
    content: "";
    position: absolute;
    left: -2px;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 3px;
    border-radius: 20px;
    background-color: #5C7065;
    color: #fff;
    transition: all 0.3s ease-out;
  }
}

@media screen and (min-width: 768px){
  .btn-secondary {
    max-width: -webkit-max-content;
    max-width: max-content;
    color: #FFC935;
    border: 1px solid #FFC935;
  }
  .btn-secondary:active {
    background-color: transparent;
    color: #FFC935;
    border: 1px solid #FFC935;
  }
  .pref-item {
    padding-left: 0;
  }
  .about-us-item {
    max-width: 298px;
  }
  .header-backround {
    padding-top: max(12px, min(2.3vw, 32px));
    padding-bottom: 0;
    background-color: transparent;
    border-radius: 0;
  }
  .header-navigation {
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: clamp(12px, -0.4rem + 4vw, 56px);
    padding-right: clamp(12px, -0.4rem + 4vw, 56px);
  }
  .nav-list {
    display: flex;
    gap: clamp(12px, -3.877rem + 9.64vw, 44px);
  }
  .header-link-phone {
    display: inline-block;
  }
  .burger-button {
    display: none;
  }
  .upper-footer {
    flex-wrap: nowrap;
  }
  .footer-map {
    max-width: 672px;
    height: inherit;
  }
  .contacts {
    max-width: 672px;
  }
  .footer-copyright {
    margin-bottom: 32px;
  }
  .burger-list {
    display: none;
  }
  .calc-type {
    flex-direction: row;
  }
  .calc-type-label {
    width: 100%;
    max-width: 491px;
  }
  .calc-select-wrapper.step-two {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .calc-select-label.drill-step-one {
    max-width: clamp(300px, -1.94rem + 43.1vw, 400px);
  }
  .calc-next-step {
    max-width: -webkit-max-content;
    max-width: max-content;
  }
