ve-text;\n    border: $tab-active-border;\n  }",".pagination-item{\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  \n    // font-size: clamp(18px, 0.5rem + 1.5vw, 20px);\n    font-size: 20px;\n    font-weight: 400;\n    // line-height: clamp(24px, 0.8rem + 1.8vw, 28px);\n    line-height: 1.4;\n  \n    color: $pagination-text;\n    background-color: $pagination-bg;\n    border-radius: $frame-rad-40;\n  \n    // width: clamp(48px, 1.5rem + 4vw, 60px);\n    // height: clamp(48px, 1.5rem + 4vw, 60px);\n    width: 60px;\n    height: 60px;\n  \n    cursor: pointer;\n  \n    &.active{\n      color: $pagination-active-text;\n      background-color: $pagination-active-bg;\n    }\n  \n  }",".pref-item{  \n    position: relative;\n    padding-left: 12px;\n  \n    @include tablet{\n      padding-left: 0;\n    }\n  \n  }\n  \n  .pref-item-svg-container{\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  \n  \n  \n    position: relative;\n    z-index: 1;\n    \n    &::before{\n      content: '';\n  \n      width: clamp(60px, 2rem + 5vw, 86px);\n      height: clamp(60px, 2rem + 5vw, 86px);\n      background-color: $pref-card-circle;\n      border-radius: 50%;\n  \n      position: absolute;\n      top: 50%;\n      right: -50%;\n      transform: translate(-50%,-50%);\n      z-index: -1;\n    }\n  \n  }\n  .pref-item-svg{  \n    // width: clamp(100px, 4rem + 7vw, 150px);\n    // height: clamp(100px, 4rem + 7vw, 150px);\n    width: 60px;\n    height: 60px;\n  \n    padding: clamp(19px, 0.4rem + 3.3vw, 43px);\n  \n    background-color: $main-bg;\n    border:$pref-card-border;\n    border-radius: $frame-rad-12;\n  \n    box-sizing: content-box;\n  }\n  .pref-item-title{\n    margin-top: clamp(16px, 0.6rem + 1.1vw, 24px);\n    margin-bottom: 12px;\n  }\n  .pref-item-descr{\n  \n  }",".about-us-item{\n    padding: 24px;\n    width: 100%;\n  \n    text-align: center;\n    \n    border: $about-us-border;\n    border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n  \n    @include tablet{\n      max-width: 298px;\n    }\n  \n    &-svg{\n      width: clamp(40px, 1.2rem + 5vw, 60px);\n      height: clamp(40px, 1.2rem + 5vw, 60px);\n  \n      &.since{\n        width: clamp(72px, 2.5rem + 7vw, 104px);\n        height: clamp(40px, 1.5rem + 3vw, 60px);\n      }\n    }\n    \n  \n    &-title{\n      font-family: $russo;\n  \n      font-size: clamp(24px, 0.5rem + 3.5vw, 40px);\n      line-height: 1.4;\n      font-weight: 400;\n      color: $about-us-subheader-text;\n  \n      margin-top: 16px;\n      margin-bottom: 12px;\n    }\n  }",".label-input-with-icon{\n    position: relative;\n  \n    display: flex;\n    flex-direction: column;\n  \n    width: 100%;\n    max-width: 524px;\n    \n    .label-icon{\n      position: absolute;\n      top: 56px;\n      left: 24px;\n      fill: #3C4942;\n      stroke: #3C4942;\n      z-index: 1;\n    }\n  \n    .input-with-icon{\n  \n      margin-top: 12px;\n  \n      outline: none;\n      border: none;\n      border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n      \n      color: $input-select-text;\n  \n      padding-top: 16px;\n      padding-bottom: 16px;\n      padding-left: 60px;\n      padding-right: 24px;\n  \n      &::placeholder{\n        color:$input-placeholder-text\n      }\n    }\n  \n  }\n  \n  .label-textarea{\n  \n    display: flex;\n    flex-direction: column;\n  \n    width: 100%;\n    max-width: 524px;\n  \n    .textarea-field{\n      margin-top: 12px;\n  \n      outline: none;\n      border: none;\n      border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n      \n      color: $input-select-text;\n  \n      padding: 16px 24px;\n  \n      position: relative;\n  \n      &::placeholder{\n        color:$input-placeholder-text\n      }\n    }\n  \n  }","header{\n    position: relative;\n    z-index: 11;\n    margin: 0 auto;\n}\n\n\n.header-backround{\n    background-color: $card-bg;\n    border-bottom-left-radius: clamp($frame-rad-16,2vw,$frame-